<template>
  <div class="load">
    <a-spin :spinning="detailFormState.spinning" tip="Loading...">
      <div class="process-box">
        <!-- 标题 -->
        <div class="header-box">
          <BillTitle :options="billTitleOption" />
          <WfApproveBox
            v-if="detailFormState.status != 10"
            @save="onSave"
            @submit="onSubmit"
            :processInstanceId="detailFormState.processInstanceId"
            :processStatus="detailFormState.status"
          />
        </div>
        <!-- 表单内容 -->
        <div class="content">
          <a-form ref="formRef" :model="detailFormState" labelAlign="right" autocomplete="off">
            <a-card title="基本信息" :bordered="false">
              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="stationName"
                    label="电站名称"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入电站名称' }]"
                  >
                    <a-input
                      disabled
                      v-model:value="detailFormState.stationName"
                      placeholder="请输入电站名称"
                    />
                    <a-input disabled v-model:value="detailFormState.id" v-if="false" />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="periodName"
                    label="期数"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入期数' }]"
                  >
                    <a-input
                      disabled
                      v-model:value="detailFormState.periodName"
                      placeholder="请输入期数"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="fileType"
                    label="类型"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择类型' }]"
                  >
                    <DictSelectBox
                      :type="`poSafetyFileType`"
                      @change="`()=> detailFormState.fileType = value`"
                      v-model:value="detailFormState.fileType"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="particularYear"
                    label="年度"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择年度' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.particularYear"
                      value-format="YYYY"
                      style="min-width: 100%"
                      picker="year"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="completionDate"
                    label="完成时间"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择完成时间' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.completionDate"
                      value-format="YYYY-MM"
                      picker="month"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isRectificationCompleted"
                    label="整改是否完成"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入整改是否完成' }]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isRectificationCompleted = value`"
                      v-model:value="detailFormState.isRectificationCompleted"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="serviceCompany"
                    label="服务单位"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入服务单位' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.serviceCompany"
                      placeholder="请输入服务单位"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isPowerGridFiling"
                    label="电网备案"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择电网备案' }]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isPowerGridFiling = value`"
                      v-model:value="detailFormState.isPowerGridFiling"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="handledByName"
                    label="经办人"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: false, message: '请输入经办人' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.handledByName"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="rectificationPlan"
                    label="遗留整改计划"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: true, message: '请输入遗留整改计划' }]"
                  >
                    <a-textarea
                      v-model:value="detailFormState.rectificationPlan"
                      row="8"
                      show-count
                      style="height: 200px"
                      :disabled="detailFormState.status != 0"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>
            </a-card>
            <a-card title="上传附件" :bordered="false">
              <div style="margin: 5px 16px 20px 10px">
                <UploadTable
                  ref="uploadTable"
                  :rows="uploadRows"
                  :colwidths="['15%', '70%', '15%']"
                  :application="`po`"
                  :module="`safetyFiles`"
                  :mode="detailFormState.status == 0 ? `normal` : `view`"
                />
              </div>
            </a-card>
          </a-form>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted, unref } from 'vue';
  import { saveData, initInfoItems, getIndex } from './safetyFiles';
  import { FormInstance, message } from 'ant-design-vue';
  import { useRouter } from 'vue-router';
  import WfApproveBox from '/@/components/Framework/WorkFlow/WfApproveBox.vue';
  import { assign } from 'lodash-es';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';
  import { BasicTable, useTable, BasicColumn, EditRecordRow } from '/@/components/Table';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import * as FileApi from '@/api/infra/file';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import { closeCurrentTab } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import UploadTable from '/@/components/Framework/Table/UploadTable.vue';
  import { useUserStore } from '/@/store/modules/user';
  import { Memory } from '@/router/index';

  const detailFormState = reactive({ spinning: false });
  const userStore = useUserStore();
  const getUserInfo = userStore.getUserInfo;
  const uploadRows = ref<any[]>([]);
  const uploadTable = ref();
  const disabledInput = ref<boolean>(true);
  const billTitleOption = reactive<any>({});
  billTitleOption.subject = '安全信息';
  billTitleOption.infoItems = [];
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = Memory.getInstance().getQuery(); // route.query;

  const formRef = ref<FormInstance>();

  function save(status) {
    detailFormState.spinning = true;
    try {
      status = status || 0;

      formRef.value.validateFields().then(() => {
        detailFormState.status = status;
        saveData(detailFormState).then((data) => {
          detailFormState.id = data;
          getIndex({ id: data }).then((data) => {
            assign(detailFormState, data.result);
          });
          message.success(status == 1 ? '提交成功。' : '保存成功');
          MsgManager.getInstance().sendMsg('safetyFiles', {});
          if (status == 1) {
            disabledInput.value = true;
            closeCurrentTab();
          }
        });
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      detailFormState.spinning = false;
    }
  }

  function onSubmit() {
    save(1);
  }

  function onSave() {
    save(0);
  }

  onMounted(async () => {
    query.id = await getBizParamId();

    await getIndex({ id: query.id }).then((data) => {
      if (query.id) {
        assign(detailFormState, data.result);
        if (
          detailFormState.status == 0 &&
          detailFormState.personMemberId.substring(0, 32) != getUserInfo.userId
        ) {
          detailFormState.status = 10;
        }
      } else {
        assign(detailFormState, query);
        detailFormState.spinning = false;
        detailFormState.status = 0;
        detailFormState.billCode = data.result.billCode;
        detailFormState.personMemberName = data.result.personMemberName;
        detailFormState.fillinDate = data.result.fillinDate;
        detailFormState.deptName = data.result.deptName;
      }

      if (detailFormState.status == 1) {
        disabledInput.value = true;
      }
    });

    if (!query.id) {
      disabledInput.value = false;
    }
    await getRecordable();

    if (
      detailFormState.bizFileId == null ||
      detailFormState.bizFileId == undefined ||
      detailFormState.bizFileId == ''
    ) {
      getBizFileId();
    } else {
      handleAttachmentCreate();
    }
  });

  const initUploadRows = (bizId: string) => {
    const fileTypes = ['报告', '整改清单'];
    const fileKindIds = ['report', 'list'];
    const base = {
      fileKindId: '',
      filetype: '',
      bizId: bizId,
      maxCount: 20,
      maxSize: 20 * 1024 * 1024,
      required: false,
      readonly: true,
    };
    const rows = [] as any;

    for (let i = 0; i < 2; i++) {
      let row = { ...base };
      row.fileKindId = fileKindIds[i];
      row.filetype = fileTypes[i];
      row.readonly = disabledInput.value;
      rows.push(row);
    }

    uploadRows.value = rows;
  };

  const getBizParamId = async () => {
    let id = query.id;
    if (!id && query.processInstanceId) {
      const processInstance = await ProcessInstanceApi.getProcessInstance(
        query.processInstanceId as string,
      );
      const businessKey = processInstance.businessKey;
      query.id = businessKey;
      id = businessKey;
    }
    return id;
  };

  const getRecordable = async () => {
    disabledInput.value = detailFormState.status != 0;
    billTitleOption.title = '安全信息';
    detailFormState.processInstanceId = query.processInstanceId;
    let { billCode, fillinDate, personMemberName, deptName } = detailFormState;
    assign(billTitleOption, { billCode, fillinDate, personMemberName, deptName });
  };

  assign(detailFormState, {
    processInstanceId: '',
    id: '',
    billCode: '',
    fillinDate: '',
    organId: '',
    organName: '',
    deptId: '',
    deptName: '',
    positionId: '',
    positionName: '',
    personMemberId: '',
    personMemberName: '',
    fullId: '',
    status: 0,
    subject: '',
    finishedDate: '',
    fileType: '',
    particularYear: '',
    completionDate: '',
    isRectificationCompleted: '',
    serviceCompany: '',
    isPowerGridFiling: '',
    handledByName: '',
    rectificationPlan: '',
    bizFileId: '',
  });
  //==================================附件资料  begin ==================================

  const getBizFileId = async () => {
    FileApi.getBizId().then((res) => {
      detailFormState.bizFileId = res;
      handleAttachmentCreate();
    });
  };

  const fileList = ref([]);

  const handleAttachmentCreate = async () => {
    initUploadRows(detailFormState.bizFileId);
  };

  const attachmentColumns: BasicColumn[] = [
    {
      title: '附件类型',
      dataIndex: 'fileType',
      align: 'left',
      width: 150,
    },
    {
      title: '附件',
      dataIndex: 'bizFileId',
      align: 'left',
      width: 600,
    },
  ];

  const [registerAttachmentTable, { getDataSource: getAttachmentDataSource }] = useTable({
    columns: attachmentColumns,
    pagination: false,
    showIndexColumn: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    dataSource: fileList,
  });
  //==================================附件资料  end ==================================
</script>
<style lang="less" scoped></style>
